.notification-container {
	position: relative;
	display: inline-block;
}

.notification-button {
	position: relative;
	background-color: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
}

.notification-icon {
	width: 50px;
	height: 50px;
}

.notification-badge {
	position: absolute;
	top: 0;
	right: 0;
	background-color: red;
	color: white;
	border-radius: 50%;
	font-size: 12px;
	padding: 5px;
	min-width: 20px;
	height: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
}

.notification-dropdown {
	position: absolute;
	top: 50px;
	right: 0;
	background-color: white;
	border: 1px solid #ccc;
	border-radius: 5px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
	width: 250px;
	z-index: 1000;
}

	.notification-dropdown ul {
		list-style: none;
		padding: 10px;
		margin: 0;
	}

	.notification-dropdown li {
		padding: 10px;
		border-bottom: 1px solid #ccc;
	}

		.notification-dropdown li:last-child {
			border-bottom: none;
		}

.mark-read-button {
	display: block;
	width: 100%;
	background-color: #4c1a2a;
	color: white;
	border: none;
	padding: 10px;
	border-radius: 0 0 5px 5px;
	text-align: center;
	cursor: pointer;
}
